import React from 'react';


export const CalcRowHead = props => {
  return (
    <div className="calc-row head-row">
      <div className="calc-col count-col">#</div>
      <div className="calc-col image-col"></div>
      <div className="calc-col name-col"></div>
      <div className="calc-col quantity-col"></div>
      <div className="calc-col price-col"><br />{props.currency}</div>
      <div className="calc-col total-col"><br />{props.currency}</div>
      <div className="calc-col control-col">#</div>
    </div>
  );
}
